import React, {Fragment, useContext, useState, useEffect} from 'react';
import './Opciones.css';
import {Link} from 'react-router-dom';

import Banner from 'react-js-banner'
import { UserContext } from '../../UserContext'
import {useHistory} from "react-router-dom";
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import InfoStripe from './InfoStripe';
import BillingInfo from './BillingInfo';

const Opciones = ()=> {
    return(

        <Fragment>
            <Items/>

        </Fragment>
    );
}

function Items() {

    let history = useHistory()
    const [items, setItems] = useState([])
    const {user, setUser} = useContext(UserContext);
    const [days, setDays] = useState(null);
    const [tiendaID, setID] = useState(null);
    const [subscripcion, setSubscripcion] = useState()
    const [show, setShow] = useState(false);

    useEffect(()=>{
        let isMounted = true;
        if(isMounted){
            fetchitems()
            .then(json=>{
                setItems(json.items);
                setDays(json.days);
                setID(json.items[0]['id'])
            })
        }
        return ()=>{isMounted=false};
        
    }, []);

    const fetchitems = async (id)=>{
        const data = await fetch(`https://tiendas-unison-web.herokuapp.com/api/v1/tiendafecha/${user.id}`);
        const items = await data.json();
        console.log(items)
        let finaldate = new Date();
        let today = new Date(Date.now())
        console.log("today:", today)
        let subdate = new Date(items[0].fechaSub)
        finaldate.setDate(subdate.getDate())
        console.log("subdate", subdate)
        console.log("finaldate", finaldate)
        let diffTime = Math.abs(finaldate - today);
        let diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
        console.log(diffDays)
        const json = {
            items: items,
            days: diffDays
        }
        return json
    };

    const eliminar = async (id)=>{
        try{
            const response = await fetch(`https://tiendas-unison-web.herokuapp.com/api/v1/tiendas/${id}`,
            {
                method: "DELETE",
            });
            //setItems(items.filter(item => item.id !== id));
            localStorage.removeItem('token.tuw')
            alert('Hasta la proxima')
            setUser(null);
            history.push("/")
        }catch(err){
            console.error(err)
        }
    }


    const checkSubscription = ()=>{
        if(subscripcion){
            if(subscripcion.status == "trialing" && subscripcion.cancel_at_period_end===true){
                return <Banner title={`Usted se encuentra en periodo de prueba`} />
            }else{
                return null
            }
        }else{
            return null
        }
    }

    return(
        <div className="mainContainer">

            {checkSubscription()}
           
            {/* <Banner title= {date} visibleTime={3000}/> */}




            <div className="itemsContainer">
                <button className="boton">Cuenta</button>
                <Link to='/panel/Menu'>
                <button className="boton">Menu</button>
                </Link>
                <Link to="/panel/misReportes">
                <button className="boton">Mis Reportes</button>
                </Link>
            </div>
            <hr />
            <div className="itemsContainer">

                <Link to='panel/MiInfo'>
                    <button className="boton">Mi informaci√≥n</button>
                </Link>


                <Link to='panel/Pedidos'>
                    <button className="boton">Pedidos</button>
                 </Link>

                <Link to="panel/misVentas">
                <button className="boton">Ventas</button>

                </Link>

                
            </div>
            <hr />
            <div className="itemsContainer">
                {/* <Link to='panel/Tarjeta'> */}
                    <button className="boton">Tarjeta</button>
                {/* </Link> */}
                <button className="boton" data-toggle="modal" href="#Modal">Baja</button>
                <button className="boton">Ayuda</button>
            </div>
            <div id="Modal" class="modal fade">
                <div class="modal-dialog">
                    <div class="modal-content">
                        <div class="modal-header">
                            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
                            <h4 class="modal-title">¬øEst√°s seguro?</h4>
                        </div>
                        <div class="modal-body">
                            <p>¬øSeguro que quieres darte te baja?</p>
                            <p class="text-warning"><small>Si lo borras, nunca podr√°s recuperarlo.</small></p>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-default" data-dismiss="modal">Cerrar</button>
                            <button type="button" class="btn btn-danger" data-dismiss="modal" onClick={()=>eliminar(user.id)}>Eliminar</button>
                        </div>
                    </div>
                </div>
            </div>
            {tiendaID ? 
            <Fragment>
                <BillingInfo id_tienda={tiendaID} show={show} setShow={setShow} setSubscripcion={setSubscripcion}/>

            {show?null:
             <InfoStripe id_tienda={tiendaID} />
            }
            </Fragment>
            : null}
        </div>


    );
}

export default Opciones;
